@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 1.8rem;
  width: 2.2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button__line {
  width: 2.2rem;
  height: 3px;
  background: #1ba49c;
  border-radius: 4px;
}
.navigation {
  font-family: 'Varela Round', sans-serif;
}
.flag-container {
  margin-bottom: 0.8rem;
  cursor: pointer
}
.header {
  padding: 2rem 1.5rem 0rem 1.5rem;
  border: 2px solid #f3f3f3;
  border-top: 0px;
}

.header__logo {
  font-family: 'Anton', sans-serif;
  font-size: 2rem;

  width: 0;
  height: 0;
  border: 50px solid transparent;
  border-bottom-color: #1ba49c;
  position: absolute;
  top: -50px;
  margin-top: 10px;
}

.header__logo:after {
  font-family: 'Anton', sans-serif;
  font-size: 2rem;

  content: '';
  position: absolute;
  left: -50px;
  top: 49px;
  width: 0;
  height: 0;
  border: 50px solid transparent;
  border-top-color: #1ba49c;
}

.logo {
  color: #fff;
  position: absolute;

  top: 33px;
  right: -21px;
  z-index: 1;
}

.header__navigation {
  display: flex;
  align-items: center;
  height: 100%;
}

.header__navigation-items ul {
  display: flex;
}

.header__navigation-items li {
  margin: 0 0.8rem;
  color: rgba(82,93,104,.7);
  padding-bottom: 1rem;
}

.header__navigation-items li:hover {
  color: #1ba49c;
}

.spacer {
  flex: 1 1;
}

@media (min-width: 769px) {
  .header__toggle-button {
    display: none;
  }
}

@media (max-width: 769px) {
  .header__navigation-items {
    display: none;
  }

  .header {
    border: none;
  }

  .header__toggle-button {
    margin-bottom: 1rem;
    margin-left: 1rem;
  }
  
  .header__logo {
    font-family: 'Anton', sans-serif;
    font-size: 2rem;
  
    width: 0;
    height: 0;
    border: 50px solid transparent;
    border-bottom-color: #1ba49c;
    position: absolute;
    top: -42px;
    margin-top: 10px;
  }
  
  .header__logo:after {
    font-family: 'Anton', sans-serif;
    font-size: 2rem;
  
    content: '';
    position: absolute;
    left: -50px;
    top: 49px;
    width: 0;
    height: 0;
    border: 50px solid transparent;
    border-top-color: #1ba49c;
  }
  
  .logo {
    color: #fff;
    position: absolute;
  
    top: 33px;
    right: -21px;
    z-index: 1;
  }
}

.side-drawer {
  height: 100%;
  background: #f8f8f8 url(/static/media/background.a6c35682.png) repeat;
  color: rgb(94, 94, 94);
  box-shadow: -3px 0px 5px rgb(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  right: 0;
  width: 70%;
  max-width: 300px;
  z-index: 200;
  transform: translateX(103%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}

.side-drawer ul {
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
}

.side-drawer li {
  margin: 1rem;
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.301);
  z-index: 100;
}
.presentation {
  font-family: 'Anton', sans-serif;
  text-align: center;
  margin: 6rem 0rem 3rem 0rem;
}

.presentation__text {
  font-size: 4rem;
  line-height: 5rem;
}

.presentation__first-line {
  display: inline-block;
}

.presentation__second-line {
  font-size: 3rem;
  color: #1ba49c;
}

.presentation__second-line span {
  text-decoration: underline;
}


@media (max-width: 900px) {
  .presentation {
    margin: 6rem 1rem 3rem 1rem;
  }
  
  .presentation__text {
    font-size: 3.2rem;
    line-height: 4rem;
  }

  .presentation__second-line {
    font-size: 2.2rem;
    line-height: 3rem;

  }
}

@media (max-width: 425px) {
  .presentation {
    margin: 3rem 0.5rem 1.5rem 0.5rem;
  }
  
  .presentation__text {
    font-size: 2.2rem;
    line-height: 3rem;
  }

  .presentation__second-line {
    font-size: 1.8rem;
    line-height: 2.4rem;
  }
}

.presentation__logos {
  font-size: 2.5rem;
}

.presentation__logos a {
  margin: 0 0.5rem;
}

.github-logo {
  color: #000;
}

.linkedin-logo {
  color: #2867b2;
}
.project-item__header {
  font-size: 1.4rem;
  text-align: left;
  margin-bottom: 1rem;
  padding-bottom: 0.4rem;
  color: #1ba49c;
  border-bottom: solid 1px #d3d3d3;
  font-family: 'Anton', sans-serif;
}
.project-item__carousel {
  width: 40%;
}

@media (max-width: 900px) {
  .project-item__carousel {
    width: 70%;
  }
}

@media (max-width: 500px) {
  .project-item__carousel {
    width: 100%;
  }
}
.project-item__content__infos {
  margin: 0 1rem;
  text-align: left;
}

.project-item__content__infos h3 {
  font-family: 'Anton', sans-serif;
  font-size: 1.3rem;
  text-decoration: underline;
  margin-bottom: 1rem;
  color: #1ba49c;
}

.project-item__content__infos ul {
  margin-bottom: 1rem;
}

.project-item__content__infos li {
  margin-bottom: 0.3rem;
}

.btn-code, .btn-website {
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  width: 225px;
  border: none;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  font-weight: 900;
  border-radius: 2px;
}

.project-item__content__infos__buttons {
  margin: 1.5rem 0;
}

.project-item__content__infos__buttons a {
  margin-right: 2rem;
  margin-bottom: 1rem;
  cursor: pointer;
}

.btn-code {
  border: solid 2px #1ba49c;
  color: #1ba49c
}

.btn-code:hover {
  border: solid 2px #1ba49c;
  background-color: #1ba49c;
  color: #fff;
}

.btn-website {
  border: solid 2px #1ba49c;
  background: #1ba49c;
  background: linear-gradient(90deg, rgba(6,188,155,1) 0%, rgba(27,164,156,1) 100%);
  color: #fff;
}

.btn-website:hover {
  border: solid 2px #1ba49c;
  background: #1ba49c;
  background: linear-gradient(90deg, rgba(27,164,156,1) 0%, rgba(6,188,155,1) 100%);
  color: #fff;
}
.project-item {
  margin: auto;
